#centerCircle {
  position: absolute;
  border-radius: 40%;
  width: 20px;
  height: 20px;
  top: 28rem;
  left: 46.7rem;
  opacity: .75;
  background-color: black;
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#circle1 {
  opacity: .75;
  position: absolute;
  top: 14rem;
  left: 41rem;
  display: block;
  width: 200px;
  height: 200px;
  border-radius: 40%;
  background-color: black;
}

#colorName1 {
  transition-duration: 1s;
  opacity: 0;
  font-family: roboto;
  font-size: 20px;
  top: 40%;
  text-align: center;
}

#colorName1:hover {
  opacity: 1;
