@-webkit-keyframes pulse {

  0%,
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(.75);
            transform: scale(.75);
  }
}

@keyframes pulse {

  0%,
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    -webkit-transform: scale(.75);
            transform: scale(.75);
  }
}
